var render = function render(){var _vm=this,_c=_vm._self._c;return _c('aside',[_vm._ssrNode("<h1 class=\"is-size-1 is-flex is-justify-content-center\">Quem sou eu?</h1> "),_c('timeline-item',{attrs:{"statusIcon":"school","title":"Ensino Médio | 2013 - 2015","statusSize":"large","statusColor":"has-background-primary"}},[_c('p',{staticClass:"has-text-justified has-text-quinary"},[_vm._v("\n      Desde os meus 6 anos, quando eu aprendi a ler, eu gostava de aprender\n      sobre as coisas, e minha brincadeira favorita era ensinar um \"público\"\n      invisível que assistia eu ministrar aulas riscando em um portão de ferro\n      da minha casa. Mas até então, não tinha vontade de ser professora, mas\n      sempre tive paixão pelos estudos e até hoje me arrepio de orgulho quando\n      vejo pessoas que venceram e conquistaram coisas com a educação.\n      Passaram-se os anos e ganhei meu primeiro computador, nesse tempo a\n      primeira linguagem que eu tive contato foi o HTML. Mas no ensino médio\n      eu dei passos em direção a outros caminhos, eu pensei querer se\n      enfemeira, até caminhei uma parte boa dessa trilha, porém, oportunidades\n      de se tornar uma dev surgiram após essa época da minha vida.\n    ")])]),_vm._ssrNode(" "),_c('timeline-item',{attrs:{"statusIcon":"airplane","title":"Intercâmbio Linguístico | 2018","stepValue":"1","statusSize":"large","statusColor":"has-background-primary"}},[_c('p',{staticClass:"has-text-justified has-text-quinary"},[_vm._v("\n      Essa história é muito boa e tenho muito orgulho, eu consegui através dos\n      meus estudos! O que eu gosto de frizar é: ESTUDOS! Eram oito mil alunos\n      incritos no Academia Enem para 100 vagas de intercâmbio para a Espanha e\n      Inglaterra e eu consegui. Me orgulho muito disso. Nesse intercâmbio\n      aprendi um pouco de espanhol e de bônus conheci gente do mundo todo.\n      Apesar de curta foi uma experiência incrível. Mas tive contato com gente\n      de diversas áreas e pude ter muitas conversas legais sobre algumas\n      carreiras profissionais. Fui com uma bagagem de roupas e voltei com\n      muito mais que isso. Eu tinha que começar a traçar um plano de carreira!\n    ")])]),_vm._ssrNode(" "),_c('timeline-item',{attrs:{"statusIcon":"account-hard-hat","title":"Primeiro Emprego | 2020","stepValue":"3","statusSize":"large","statusColor":"has-background-primary"}},[_c('p',{staticClass:"has-text-justified has-text-justified has-text-quinary"},[_vm._v("\n      Meu primeiro emprego foi em uma fábrica de calçados. Eu fiz parte de uma\n      turma de aprendizes que trabalhava na produção e eu amava cada dia de\n      trabalho e nossas aulas durante a tarde. O projeto final foi desenhar e\n      confeccionar um modelo novo de sapato para o desfile final. Apesar de\n      ter sido uma experiência legal, foi durante esse período que decidi\n      começar na faculdade de Análise e Desenvolvimento de Sistemas, eu estava\n      o tempo todo pensando em construir uma carreira sólida que me abrisse\n      oportunidades não só no Brasil e que eu fizesse parte de um grupo de\n      profissionais que fazem trabalhos incríveis que facilitam e revolucionam\n      a vida das pessoas através da tecnologia.\n    ")])]),_vm._ssrNode(" "),_c('timeline-item',{attrs:{"statusIcon":"school","title":"Graduação | 2020 - Atualmente","stepValue":"1","statusSize":"large","statusColor":"has-background-primary"}},[_c('p',{staticClass:"has-text-justified has-text-quinary"},[_vm._v("\n      Durante a graduação eu comecei a fazer projetos, principalmente focados\n      em front-end, tive ajuda de profissionais incríveis que estão a muito\n      tempo na carreira, participei de bootcamps e cursos voltados para área\n      que me fizeram abrir muito mais a mente e saber das possíbilidades do\n      que nós da área tech podemos ser capazes de fazer e o quanto nosso\n      trabalho é importante para todas as áreas. \n    ")])])],2)
